.book-container {
	display: flex;
	height: 100%;
	flex-direction: column;
	justify-content: space-between;
}

.header {
	display: flex;
	justify-content: space-between;
	margin: 15px 15px 0 15px;
}

.header__back-button {
	width: 40px;
	height: 40px;
	background-color: #323232;
	border: 0;
	padding: 0;
	margin: 0 0 0 0;
	outline: none;
	border-radius: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.back-icon {
	width: 22px;
	height: 22px;
	padding: 0 0 0 8px;
}

.header-title {
	color: #DCDCDC;
	padding: 10px 15px 10px 15px;
	background-color: #323232;
	margin: 0;
	border-radius: 100px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 14px;
}

.body {

}

.body-text {
	color: #FFF1CD;
	font-size: 27px;
	margin: 0 0 0 0;
	font-weight: 500;
}

.footer {
	padding: 20px 15px 20px 15px;
	background-color: #323232;
	border: 1px solid #424242;
	border-radius: 20px 20px 0px 0px;
}

.footer-container {
	display: flex;
	justify-content: space-between;
	margin: 0 0 20px 0;
}

.footer__volume-container {
	background-color: transparent;
	padding: 0 0 0 0;
	margin: 0 0 0 0;
	border: 0;
}

.volume-icon {
	width: 30px;
	height: 30px;
}

.footer-speed {
	
}

.footer__player-container {
	display: flex;
	align-items: center;
	justify-content: center;
}

.footer__button-redo {
	margin: 0 0 0 0;
	padding: 10px;
	background-color: #141414;
	border: 1px solid #424242;
	border-radius: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.redo-icon {
	width: 30px;
	height: 30px;
}

.footer__button-pause {
	padding: 20px;
	margin: 0 30px 0 30px;
	background-color: #141414;
	border: 1px solid #424242;
	border-radius: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.pause-icon {
	width: 40px;
	height: 40px;
}

.redo-round {
	-moz-transform: scale(-1, 1);
	-o-transform: scale(-1, 1);
	-webkit-transform: scale(-1, 1);
	transform: scale(-1, 1);
}