.home-container {
	margin: 20px 20px 0 20px;
}

.home-title {
	color: #fff;
	font-size: 30px;
	margin: 0 0 30px 0;
	font-weight: 600;
	text-align: left;
}

.home-book__container {
	display: flex;
}

.book-link {
	width: 50%;
	margin: 0 15px 0 0;
}
.book-link:last-child {
	margin: 0;
}

.book-img {
	width: 100%;
	height: 100%;
	border-radius: 4px;
}